import myformCard from "../../../components/myformCard.vue"
import popup from "../../../components/popup.vue"
import store from "../../../store/index.js"
export default {
	data() {
		return {
			//显示或者选中的变量
			checked: {
				first: true,
				second: false,
				thrid: false,
				four: false
			},
			revealChecked: {
				first: false,
				second: true,
				thrid: true
			},
			//各个状态的数量
			num:[0,0,0],
			//时间戳
			sjc: '',
			//
			jclx: '',
			wtrq:'',
			//草稿箱的表单卡
			actlistCao: [],
			//待结算的表单卡
			actlistDai: [],
			//已结算的表单卡
			actlistYi: [],
			//表单卡的状态
			state:'',
			//表单卡头标的颜色
			titleColor:''
		}
	},
	components: {
		myformCard,
		popup
	},
	//加载时获取order页面传递的sjc，根据时间戳获取送检委托所有的表单数据
	onLoad: function(option) {
		this.sjc = option.sjc;
		this.wtrq=option.wtrq;
	},
	// onShow:function() {
	// 	this.onLoad();
	// 	console.log(2223);
	// },
	methods: {
		//三个状态按钮选中的切换
		replace(e) {
			if (e == 1) {
				this.checked = {
						first: true,
						second: false,
						thrid: false,
						four: false
					},
					this.revealChecked = {
						first: false,
						second: true,
						thrid: true,
					}

			} else if (e == 2) {
				this.checked = {
						first: false,
						second: true,
						thrid: false,
						four: true
					},
					this.revealChecked = {
						first: true,
						second: false,
						thrid: true,
					}

			} else {
				this.checked = {
						first: false,
						second: false,
						thrid: true,
						four: true
					},
					this.revealChecked = {
						first: true,
						second: true,
						thrid: false,
					}
			}
		},
		//由myformCard子组件长按选项卡触发的父组件事件，这个事件触发的是popup子组件的openPopup方法
		openPopupAff([id, state]) {
			console.log(2222);
			// location.reload();
			
			this.$refs.popup.openPopup([id, state]);
		},
		reload([id,state,e]){
			console.log(state);
			switch(state){
				case 'a7&b':
				for(var i=0;i<this.actlistCao.length;i++){
					if(this.actlistCao[i].id==id){
						console.log(this.actlistCao[i]);
						console.log(this.actlistCao[i].titleColor);
						this.actlistCao[i]['titleColor']='b';
						this.actlistCao[i]['state']='a8&b';
						this.actlistDai.push(this.actlistCao[i]);
						this.num[1]++;
					}
				    this.actlistCao.splice(i,1);
					this.num[0]--;
				}
				break;
				case 'a8&b':
				for(var i=0;i<this.actlistDai.length;i++){
					if(this.actlistDai[i].id==id){
						console.log(this.actlistDai[i]);
						console.log(this.actlistDai[i].titleColor);
						if(e=='1'){
							this.actlistDai[i]['titleColor']='r';
							this.actlistDai[i]['state']='a7&r';
							this.actlistCao.push(this.actlistDai[i]);
							this.num[0]++;
						}else{
							this.actlistDai[i]['titleColor']='b';
							this.actlistDai[i]['state']='a9&b';
							this.actlistYi.push(this.actlistDai[i]);
							this.num[2]++;
						}
						this.actlistDai.splice(i,1);
						this.num[1]--;
					}
				}
				break;
				case 'a9&b':
				for(var i=0;i<this.actlistYi.length;i++){
					if(this.actlistYi[i].id==id){
						console.log(this.actlistYi[i]);
						console.log(this.actlistYi[i].titleColor);
						if(e=='1'){
							this.actlistYi[i]['titleColor']='r';
							this.actlistYi[i]['state']='a7&r';
							this.actlistCao.push(this.actlistYi[i]);
							this.actlistYi.splice(i,1);
							this.num[0]++;
							this.num[2]--;
						}else{
							this.actlistYi[i]['titleColor']='l';
							this.actlistYi[i]['state']='a9&l';
						}
					}
				}
			}
		},
		jiaZai(){
			this.actlistCao=[];
			this.actlistDai=[];
			this.actlistYi=[];
			this.num=[0,0,0];
			uni.request({
				url: this.apiServer + '/basicdata/getmyformcard.php',
				data: {
					flag: 'report',
					sjc: this.sjc
				},
				method: 'POST',
				dataType: 'json',
				header: {
					'content-type': 'application/x-www-form-urlencoded', //POST头信息
				},
				success: (res) => {
					res = res.data;
					console.log(res);
					for (var i = 0; i < res.length; i++) {
						var l="a10||a11||a12";
					    this.state=(res[i].state||' ').split('&')[0];
						this.titleColor=(res[i].state||' ').split('&')[1];
						res[i]['firstTitle'] = '报告编号';
						res[i]['secondTitle'] = '编制日期';
						res[i]['thridTitle'] = '编制人';
						res[i]['fourTitle'] = '查看报告';
						if (this.state == 'a7') {
							this.num[0]++;
							res[i]['titleColor'] = this.titleColor;
							this.actlistCao.push(res[i]);
						} else if (this.state == 'a8') {
							this.num[1]++;
							res[i]['titleColor'] = this.titleColor;
							this.actlistDai.push(res[i]);
						} else if (this.state == 'a9') {
							this.num[2]++;
							res[i]['titleColor'] = this.titleColor;
							this.actlistYi.push(res[i]);
						} else if(l.indexOf(this.state)>-1&&this.state!='a1'){
							this.num[2]++;
							res[i]['titleColor'] = 'l';
							res[i]['state'] = 'a9&l';
							this.actlistYi.push(res[i]);
						}
					}
				},
				fail: (error) => {
					console.log(error);
				}
			});
		}
		
	},
	//页面加载或页面返回初始化状态
	onShow:function() {
		this.reveal= {
			first: true,
			second: false,
		},
		this.jiaZai();
	}
}
